#root {
  width: 100%;
  height: 100%;
}

#wrap {
  position: relative;
  width: 100%;
  min-height: 100%;
}

#header {
  z-index: 200;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  width: 100%;
  gap: 0 100px;
  // justify-content: space-between;
  height: 100px;
  padding: 0 30px;
  background: #000a23;
  h1 {
    .logo {
      @include font(38px, $white);
      font-weight: $fw-bold;
    }
  }
  .gnb {
    display: flex;
    align-items: center;
    gap: 0 30px;
    & > li {
      .btn-menu {
        @include font(24px, $white);
        font-weight: $fw-bold;
      }
    }
  }
  @media all and (max-width: 767px) {
    gap: 0 60px;
    height: 60px;
    padding: 0 20px;
    h1 {
      .logo {
        @include font(24px);
      }
    }
    .gnb {
      gap: 0 20px;
      & > li {
        .btn-menu {
          @include font(18px);
        }
      }
    }
  }
}

#container {
  width: 100%;
  padding-bottom: 30px;
}

#footer {
  @include center(self, null, null, 0, 0);
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  height: 30px;
  padding: 0 30px;
  background: $bg-ea;
  @include font(12px, $black-9);
}

.page-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid $bg-c;
  padding-bottom: 15px;
  margin-bottom: 30px;
  &.rgt {
    justify-content: flex-end;
  }
  .tit-set {
    display: flex;
    align-items: center;
    .tit {
      @include font(26px, $black-3);
      font-weight: $fw-bold;
    }
  }
  .info-set {
    display: flex;
    align-items: center;
    .btn {
      height: 40px;
      padding: 0 20px;
      background: $black-3;
      @include font(18px, $white);
    }
  }
  @media all and (max-width: 767px) {
    padding-bottom: 10px;
    margin-bottom: 20px;
    .tit-set {
      .tit {
        @include font(22px);
      }
    }
    .info-set {
      .btn {
        height: 30px;
        padding: 0 15px;
        @include font(15px);
      }
    }
  }
}

.breadcrumb {
  display: flex;
  align-items: center;
  & > li {
    display: flex;
    align-items: center;
    @include font(16px, $black-8);
    &::before {
      content: ">";
      margin: 0 10px;
      color: $black-8;
      font-weight: $fw-regular;
    }
    &.home {
      &::before {
        content: none;
      }
    }
    &.last {
      @include font(16px, $black-3);
      font-weight: $fw-bold;
    }
  }
}

.rating {
  display: flex;
  align-items: center;
  gap: 0 5px;
  .btn-rate {
    width: 20px;
    height: 20px;
    background: url("../../public/rate_def.png") no-repeat 0 0;
    background-size: 100%;
    &.active {
      background: url("../../public/rate_active.png") no-repeat 0 0;
      background-size: 100%;
    }
  }
}

.main {
  width: 100%;
  padding: 40px;
  &-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    width: 100%;
    gap: 30px;
    & > li {
      width: 100%;
      border: 1px solid $bg-ea;
      .thumb {
        width: 100%;
        height: 200px;
				background: $bg-ea;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      .txt-box {
        padding: 20px;
        .name {
          margin-top: 10px;
          @include font(20px, $black-3);
          font-weight: $fw-medium;
        }
				.address{
					margin-top: 5px;
					@include font(16px, $black-8);
				}
				.tel{
					margin-top: 10px;
					@include font(14px, $black-8);
				}
        .date {
          margin-top: 10px;
          @include font(14px, $black-9);
        }
      }
    }
  }
  .no-data {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: calc(100vh - 400px);
    @include font(20px, $black-6);
  }
  @media all and (max-width: 767px) {
    padding: 20px;
    &-list {
      grid-template-columns: repeat(1, 1fr);
      gap: 15px;
      & > li {
        .thumb {
          height: 120px;
        }
        .txt-box {
          padding: 15px;
          .name {
            margin-top: 10px;
            @include font(14px);
          }
					.address{
						@include font(12px);
					}
					.tel{
						@include font(12px);
					}
          .date {
            margin-top: 5px;
            @include font(12px);
          }
					.rating{
						gap: 0 3px;
						.btn-rate {
							width: 16px;
							height: 16px;
						}
					}
        }
      }
    }
    .no-data {
      height: calc(100vh - 400px);
      @include font(16px);
    }
  }
}

.register {
  padding: 40px;
  @media all and (max-width: 767px) {
    padding: 20px;
  }
}

.form {
  &-list {
    padding: 0 30px;
    & > li {
      display: flex;
      align-items: center;
      & + li {
        margin-top: 15px;
      }
    }
  }
  &-tit {
    flex-shrink: 0;
    width: 120px;
    @include font(18px, $black-3);
    font-weight: $fw-bold;
		&.necessary{
			&::after{
				content: '*';
				margin-left: 3px;
				color: $error;
			}
		}
  }
  &-set {
    width: 100%;
    .inp-set {
      width: 100%;
      .inp {
        width: 100%;
      }
    }
  }
  @media all and (max-width: 767px) {
    &-list {
      padding: 0;
      & > li {
        display: block;
        & + li {
          // margin-top: 15px;
        }
      }
    }
    &-tit {
      width: 100%;
			margin-bottom: 10px;
      @include font(14px);
    }
  }
}
